.wrap {
  width: 800px;
  padding-bottom: 0.2em;
  margin: 0 auto;
}
.home {
  position: relative;
  top: 4em;
}
.slowmode {
  margin: 0 auto;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.slowmode .imagetopper {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  bottom: 0;
  padding: 1em;
  border: 2px solid #ccc;
}
.imagetopper img {
  position: relative;
  top: 20px;
  line-height: 20;
  justify-content: center;
  width: 100%;
  height: 60%;
}
.slowmode h2 {
  position: relative;
  top: 0em;
  left: 12px;
  color: #011b33 !important;
  font-family: "Google Sans Bold";
  font-weight: 700 !important;
  font-size: 1.5rem;
  transition: 0.35s;
}
div .other_inf p {
  position: relative;
  margin-top: 2em;
  max-width: 400px;
  line-height: 28px;
  font-size: 17px;
  color: #011b33;
  font-family: "Google Sans Regular";
}
.meaning {
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
}
.alice-carousel__prev-btn [data-area]::after,
.alice-carousel__next-btn [data-area]::after {
  display: none !important;
}
.product_section {
  position: relative;
  display: block;
  border: 1px solid #ccc;
  width: 100%;
  height: 100%;
  margin-bottom: 1em !important;
  border-radius: 5px;
}
div.search {
  padding: 2em;
  padding-bottom: 0em;
  width: 70%;
  margin: 0 auto;
}
div.search input {
  height: 40px;
  border-radius: 2em;
  padding-left: 2.5em;
  background-color: #f1f1ff;
}
.items {
  padding: 0.2em;
  display: inline-block;
  justify-items: flex-start;
}

.search .reck {
  position: absolute;
  margin-left: 1em;
  top: 2.9em;
}
.single-item {
  position: relative;
  margin-left: 1.2em;
  margin-top: 20px;
  display: inline-block;
  border-radius: 0.3em;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 12.5em !important;
  border: 1px solid #f1f1f1;
}
.single-item img {
  width: 100%;
  height: 150px;
}
.abt-span {
  font-family: "Google Sans regular";
  color: #57584e !important;
  font-size: 15px;
  height: calc(50vh - 100px);
  position: relative;
  top: 1em;
  overflow: auto;
  text-align: left;
  font-weight: 500;
  padding-bottom: 3em;
  text-transform: normal;
}
.abt-us {
  font-family: "Google Sans Bold";
  color: #57584e !important;
  font-size: 17px;
  font-weight: 500;
  border-bottom: 1px solid #f5f5f5;
  padding-bottom: 1em;
  text-align: center;
  text-transform: capitalize;
}
.cperson {
  font-family: "Google Sans Bold";
  color: #57584e !important;
  font-size: 17px;
  position: relative;
  top: 1em;
  font-weight: 500;
  text-align: center;
  text-transform: capitalize;
}
.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 0.4rem;
  padding-right: 0.4rem;
  width: 69% !important;
}
.columns {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.4rem;
  margin-right: -0.4rem;
}
.columns .rolingball {
  display: flex;
  flex-wrap: nowrap;
}
.column {
  padding-left: 0.8rem;
  padding-right: 0.8rem;
  width: 50%;
  max-width: 100%;
  flex: none;
}
.pproducts {
  border-radius: 6px;
  height: 300px;
  margin-top: 30px;
  display: flex;
  background: rgb(0, 0, 0, 0.3) !important ;
  flex-direction: column;
  cursor: pointer;
}
.ppmedia {
  display: flex;
  padding: 20px;
  border-radius: 6px 6px 0 0;
  background: rgb(0, 0, 0, 0.1) !important ;
  align-items: center;
  justify-content: center;
  height: 230px;
  overflow: hidden;
}
.ppdata {
  background: #fff;
  border-radius: 0 0 6px 6px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 30px;
  flex-grow: 1;
}
.wrapers {
  width: 1200px;
  margin: 0 auto;
}
div.searchs {
  padding: 2em;
  padding-bottom: 0em;
  width: 71%;
  margin: 0 auto;
}
div.searchs input {
  height: 50px;
  border-radius: 0.3em;
  padding-left: 2.5em;
  font-family: "Google Sans Bold";
  font-size: 16px;
  color: #fff !important;
  text-transform: normal;
  background-color: transparent;
  border: 1px solid rgb(0, 0, 0, 0.1) !important ;
  background: rgb(0, 0, 0, 0.3) !important;
}

div.searchs input::placeholder {
  color: #fff !important;
  background-color: transparent !important;
  background: transparent !important;

  opacity: 1;
}
.searchs .recks {
  position: relative;
  color: #fff !important;
  font-size: 17px;
  margin-left: 1em;
  margin-bottom: -37px;
}
.tempb {
  background-color: #3d5084 !important;
}
.btw span {
  font-family: "Google Sans Bold";
  color: #fff !important;
  font-size: 27px;
  text-transform: capitalize;
}
.fabag {
  color: #fff;
  font-size: 25px;
  cursor: pointer;
}
.fasocial {
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
}
.fasocialss {
  position: relative;
  top: 2em;
  font-size: 2.5rem;
  cursor: pointer;
}
.fasocials {
  color: #ccc;
  font-size: 1.5rem;
  cursor: pointer;
}
.socials a {
  color: #ccc;
  font-size: 13px;
  cursor: pointer;
}
.social span {
  margin-left: 10px;
}
